.App {
  text-align: center;
  background-color: #333333;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

button {
  background-color: white;
  color: #333333;
  border: none;
  padding: 15px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  cursor: pointer;
}

.window {
  background-color: white;
  color: black;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0px 0px 10px 2px rgba(0,0,0,0.75);
}

.pushable {
  position: relative;
  padding: 0;
  border: none;
  background: none;
  cursor: pointer;
  display: inline-block;
}

.pushable .background {
  position: absolute;
  top: 4px;
  left: 4px;
  width: 100%;
  height: 100%;
  border-radius: 12px;
  background: #cc0000;
  z-index: -1;
}

.front {
  display: block;
  padding: 10px 20px;
  border-radius: 12px;
  background: #ff0000;
  color: white;
  font-weight: bold;
  will-change: transform;
  transition: transform 250ms, top 250ms, left 250ms;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.3);
  position: relative;
}

.pushable:hover .front {
  transform: translateY(-6px);
}

.pushable:active .front {
  transform: translateY(-2px);
}